.Submission {
  width: 100%;
  height: 700px;
  padding-top: 100px;
  color: #333333;
  font-family: Roboto Regular;
  .tools-challenge {
    width: 1140px;
    padding: 16px;
    margin: 0 auto;
    em {
      // text-shadow: 2px 1px 1px #000;
      background: rgba(33, 33, 33, 0.2);
      font-size: 12px;
    }
    .indent {
      padding-left: 2em;
    }
    li > span {
      font-size: 18px;
      font-weight: 600;
    }
    .bt {
      text-align: center;
    }
    h2,
    h3 {
      font-weight: 900;
      margin-top: 32px;
      margin-bottom: 16px;
      padding-bottom: 16px;
      color: #ff8800;
      border-bottom: 1px solid #ff8800;
    }
    .json {
      margin: 0;
      padding: 20px;
      background: #eee;
      border: 2px solid #333333;
    }
    .medal {
      &::after {
        content: "";
        display: block;
        clear: both;
      }
      text-align: center;
      > div {
        display: inline-block;
        width: 110px;
        margin-right: 64px;
        text-align: center;
        img {
          width: 90px;
        }
        p {
          font-weight: 600;
          padding: 0;
          margin: 0;
          text-align: center;
          font-size: 24px;
        }
      }
    }
  }
}
