.People{
    width: 100%;
    height: 500px;
    padding-top:100px ;
    font-weight: 900;
    color: #333333;
    // background-image: url("../../assets/background-3.jpg");
    // background-attachment: fixed;
    // background-position: top center;
    // background-repeat: repeat;
    // background-clip:content-box;
    .people-container{
        width: 960px;
        padding-left:120px ;
        margin: 0 auto;
        h2{
            margin: 50px 0 20px 0;
            font-size: 30px;
            font-weight: 900;
            color: #ff8800;
        }
        .people-image-warp{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            >div{
                width: 270px;
                height: 270px;
                >div{
                    width: 150px;
                    height:150px;
                    // background-image: url("../../assets/bernard.png");
                    border-radius: 50%;
                    &:hover a{
                        transform: scale(1);
                        opacity: 1;
                        transition: all 0.5s ease-in-out  ;
                    }
                    >a{
                        float: left;
                        text-align: center;
                        width: 150px;
                        width: 150px;
                        height:150px;
                        display: block;
                        opacity: 0;
                        background: rgba($color: #000000, $alpha: 0.4);
                        transform: scale(0);
                        border-radius: 50%;
                        transition: all 0.5s ease-in-out;
                        i{
                            display: block;
                            margin-top: 75px;
                            width: 100%;
                            height: 1px;
                            background: #fff;
                        }
                        p{
                            color: #bbb;
                            font-style: italic;
                            margin: 0 5px;
                            margin-top: 90px;
                            font-size: 12px;
                            border-top: 1px solid
                        }
                    }
                }
                
            }
        }
    }

}