.Participation{
    width: 100%;
    height: 700px;
    padding-top:100px ;
    color: #333333;
    
    font-family: Roboto Regular;
    .tools-challenge{
        width: 1140px;
        padding: 16px;
        margin: 0 auto;
        .bt{
            text-align: center;
        }
        h2,h3{
            font-weight: 900;
            margin-top: 32px;
            margin-bottom: 16px;
            padding-bottom: 16px;
            color: #ff8800;
            border-bottom: 1px solid #ff8800;
        }
        .json{
            margin: 0;
            padding: 20px;
            background:#eee;
            border: 2px solid #333333;
        }
        .medal{
            &::after{
                content: "";
                display: block;
                clear: both;
            }
            text-align: center;
            >div{
                display: inline-block;
                width: 110px;
                margin-right: 64px;
                text-align: center;
                img{
                    width: 90px;
                }
                p{
                    font-weight: 600;
                    padding: 0;
                    margin: 0;
                    text-align:center;
                    font-size: 24px;
                }
            }
        }
    }
}